.page {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  color: $textColor;
  background-color: #1d1d1d;
  scroll-behavior: smooth;
  
  &__body {
    margin: 0;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 80px;
    
    @include onDesktop {
      margin-bottom: 120px;
    }
  }

  &__section-title {
    margin: 0 0 48px;
    font-weight: 700;
    font-size: 32px;
    line-height: 32px;
    text-align: center;
    letter-spacing: -1.5px;

    @include onDesktop {
      font-size: 64px;
      line-height: 64px;
    }
  }
}
